.card {
    width: 15rem !important;
    /* height: 13rem; */
    position: absolute;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    /* width: 10rem; */
    text-align: center;
    background: rgba(255, 255, 255, 0.26);
    border: 4px solid orange;
    box-shadow: black;
    border-radius: 80px;
    padding: 0px 26px 4rem 26px;
  }
  .card span:nth-of-type(2) {
    color: gray;
    font-size: 16px;
  }
  .card > img {
    transform: scale(0.6);
    margin-bottom: -2rem;
  }
  .c-button {
      background: #FFFFFF;
      box-shadow: 0px 19px 60px rgba(0, 0, 0, 0.08);
      border-radius: 7px;
      border: none;
      padding: 10px;
      font-size: 16px;
      color: #5290FD;
  }
  
  .c-button:hover{
      transform: scale(1.1);
      cursor: pointer;
  }
  /* cards */
  
  @media screen and (max-width: 480px){
  .card {
    width: 11rem !important;
    /* height: 13rem; */
    position: absolute;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    /* width: 10rem; */
    text-align: center;
    background: rgba(255, 255, 255, 0.26);
    border: 3px solid orange;
    box-shadow: black;
    border-radius: 80px;
    padding: 0px 26px 4rem 26px;
  }
  }